import React from "react";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";
import styled from "styled-components";
import html5 from "../assets/html.png";
import css3 from "../assets/css.png";
import javascript from "../assets/js.png";
import python from "../assets/python.png";
import django from "../assets/django.png";
import react from "../assets/react.png";
import java from "../assets/java.png";
import android from "../assets/android.png";
import excel from "../assets/excel.png";
import codeigniter from "../assets/codeigniter.png";
import expo from "../assets/expo.png";
import php from "../assets/php.png";
const Container = styled.div`
  background: #111;
`;
const Skill = styled.div`
  border: 1px solid #555;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
`;
const Col = ({ url, onMouseMove, onMouseOut, title, width = 50 }) => (
  <div className="col">
    <Fade bottom>
      <div>
        <Skill
          title={title}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          className="skill">
          <img src={url} alt="" width={width} />
        </Skill>
      </div>
    </Fade>
  </div>
);
export default class About extends React.PureComponent {
  componentDidMount() {}
  onMouseOut(e) {
    e.preventDefault();
    e.target.style.transform = "translate(-50%,0)";
  }
  onMouseMove(e) {
    e.preventDefault();
    e.target.style.transform = `translate(calc(-50% + ${e.movementX * 2}px),${
      e.movementY * 2
    }px) scale(1.05)`;
  }
  render() {
    return (
      <Container>
        <div className="header__content">
          <Zoom>
            <div>
              <div className="header__content_line"></div>
            </div>
          </Zoom>
          <Fade bottom>
            <div className="h2 h2-light">GET TO KNOW ME!</div>
          </Fade>
          <Fade bottom>
            <div style={{ marginTop: 50 }}>
              <div
                className="about_para small-caps-2 mt-1"
                style={{
                  maxWidth: 700,
                  left: "50%",
                  position: "relative",
                  transform: "translateX(-50%)",
                }}>
                I am a 3rd year student pursuing Bachelor's of Technology in
                Computer Science. I am an avid learner and always curious to
                learn about new technologies.
              </div>
            </div>
          </Fade>
          <div className="container" style={{ marginTop: 70 }}>
            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Html5"
                url={html5}
                width={70}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="CSS3"
                url={css3}
                width={70}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={70}
                title="Javascript"
                url={javascript}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="PHP"
                url={php}
              />
            </div>
            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={90}
                title="Django"
                url={django}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Python"
                url={python}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                width={70}
                title="React"
                url={react}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Java"
                url={java}
              />
            </div>

            <div className="row">
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Android Studio"
                url={android}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Excel"
                url={excel}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="CodeIgnitor"
                url={codeigniter}
              />
              <Col
                onMouseMove={this.onMouseMove}
                onMouseOut={this.onMouseOut}
                title="Expo"
                url={expo}
              />
            </div>
          </div>
        </div>
      </Container>
    );
  }
}
